import React, { useEffect, useState } from 'react'
import PropTypes from 'prop-types'
import {connect} from 'react-redux'
import {getVillaList} from "../../../../redux/actions";
import LayoutContent from "../../../../components/layout-components/LayoutContent";
import {Avatar, Card, Col, Image, Row} from 'antd'
import {EditOutlined, EllipsisOutlined, SettingOutlined} from "@ant-design/icons";
import CardCustom from "../../../../components/shared-components/cards/card-custom";

const VillaAll = (props) => {
    let { getVillaList } = props

    useEffect(()=> {
        getVillaList({
            ...props?.params,
            withRooms:true,
        })
    },[getVillaList])

    return (
        <div style={{width: "100%", maxWidth: 1080, margin: "0 auto"}}>
            <div className={'py-10'}>
                <div className="grid grid-cols-12 gap-4">
                    {
                        props?.data?.length > 0 ? (
                            props?.data.map((item,index)=> {
                                return (
                                    <div className="col-span-4" key={`card-${index}`}>
                                        <CardCustom
                                            {...item}
                                            title={item?.name ?? item?.villaName ?? "-"}
                                            url={`/villa/p/${item?._id ?? item?.name?.replace(/ /g,'-').toString().toLowerCase() ?? "undefined"}`}
                                        />
                                    </div>
                                )
                            })
                        ): (
                            <span>Tidak ada</span>
                        )
                    }
                </div>
            </div>
        </div>
    )
}

VillaAll.propTypes = {
    getVillaList: PropTypes.func.isRequired
}
VillaAll.defaultProps = {}

export default connect(({Villa}) => {
    let {list} = Villa

    return {...list}
}, {getVillaList})(VillaAll)
