import React, {useState, useEffect} from 'react'
import PropTypes from 'prop-types'
import {connect} from 'react-redux'
import Flex from "../Flex";
import {LoadingOutlined} from "@ant-design/icons";
import {Image,Badge, Tooltip, Button, Rate} from "antd";
import {Link} from 'react-router-dom'
import {LikeOutlined, EnvironmentOutlined,CommentOutlined,HomeOutlined} from '@ant-design/icons'

const CardCustom = (props) => {
    let {groups, url, images} = props

    const [visible, setVisible] = useState(false)
    return (
        <>
            <div className="w-full min-h-[220px] shadow-xl rounded-xl bg-white w-full overflow-hidden">
                <div className="w-full h-[140px] relative rounded-xl overflow-hidden">
                    <img
                        src={[props?.thumbnail?.prefix_url,props?.thumbnail?.filename].join("/")}
                        alt={images}
                        className={'w-full h-full max-h-[180px] object-cover'}
                    />
                    <div
                        className={'absolute bottom-4 right-4 bg-black w-10 h-10 bg-opacity-50 rounded-full z-10 flex items-center justify-center'}>
                        <span className={'text-white'}>{props?.photos?.length ?? 0}</span>
                    </div>
                    <div className="absolute top-4 right-4 z-10">
                        <Button size={'small'} icon={<EnvironmentOutlined/>}>
                            <span className={'!capitalize'}>
                            {props?.locations?.districts?.name?.toString()?.toLowerCase() ?? "-"}
                            </span>
                        </Button>
                    </div>
                </div>
                <div className={'!relative'}>
                    <div className="absolute h-[100px] w-full left-0 -top-[50px]">
                        <div className="w-full flex items-center px-4 space-x-4">
                            <div className="min-w-[100px] h-[100px] rounded-full overflow-hidden">
                                <Image
                                    className={'relative z-0'}
                                    preview={{visible: false}}
                                    width={100}
                                    placeholder={
                                        <Flex alignItems={'center'} justifyContent={'center'}
                                              className={'h-100 w-100'}>
                                            <LoadingOutlined/>
                                        </Flex>
                                    }
                                    onClick={() => setVisible(true)}
                                    height={props?.size ?? 100}
                                    style={{
                                        minWidth: "100% !important",
                                        objectFit: "cover"
                                    }}
                                    src={[props?.thumbnail?.prefix_url,props?.thumbnail?.filename].join("/")}
                                />
                            </div>
                            <div className={'flex flex-col justify-between'}>
                                <Link to={url}>
                                    <h3 className={'text-white p-0 m-0 text-lg line-clamp-1'}>{props?.title ?? "-"}</h3>
                                </Link>
                                <p className={'p-0 m-0 text-md line-clamp-1'}>{props?.description ?? "-"}</p>
                            </div>
                        </div>
                    </div>

                    <div className="px-4 pt-14 space-y-2 pb-4 flex items-center justify-between w-full">
                        <Rate allowHalf defaultValue={2.5} style={{fontSize: 14, margin: 0}}/>

                        <div className="flex space-x-2">

                            <Button size={'small'} type="primary" ghost icon={<LikeOutlined/>}/>
                            <Button size={'small'} type="primary" ghost icon={<CommentOutlined />}/>
                            <Tooltip title={`Total Rooms ${props?.total_rooms ?? 0}`}>
                                <Badge count={props?.total_rooms ?? 0} showZero>
                                    <Button size={'small'} type="primary" ghost icon={<HomeOutlined />}/>
                                </Badge>
                            </Tooltip>

                        </div>
                    </div>

                </div>
            </div>
            {
                Array.isArray(groups) && groups.length > 0 ?
                    <div style={{display: 'none'}}>
                        <Image.PreviewGroup preview={{visible, onVisibleChange: vis => setVisible(vis)}}>
                            {
                                Array.isArray(groups) && groups.length > 0 &&
                                groups.map((item) => (
                                    <Image src={[item?.prefix, item?.url].join("")}/>
                                ))
                            }
                        </Image.PreviewGroup>
                    </div>
                    :
                    <div style={{display: 'none'}}>
                        <Image.PreviewGroup preview={{visible, onVisibleChange: vis => setVisible(vis)}}>
                            <Image src={images}/>
                        </Image.PreviewGroup>
                    </div>
            }

        </>

    )
}

CardCustom.propTypes = {
    title: PropTypes.string.isRequired,
    type: PropTypes.string.isRequired,
    description: PropTypes.string,
    coordinates: PropTypes.array,
    images: PropTypes.string,
    url: PropTypes.string,
    groups: PropTypes.array
}
CardCustom.defaultProps = {
    type: "default",
    groups: [],
    url: "/",
    images: "https://images.unsplash.com/photo-1662835291968-0a61659ff3cd?ixlib=rb-1.2.1&ixid=MnwxMjA3fDB8MHxwaG90by1wYWdlfHx8fGVufDB8fHx8&auto=format&fit=crop&w=870&q=80 870w"
}

export default CardCustom